import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Promotor from '../views/Promotor.vue'
import Caixa from '../views/Caixa.vue'
import Sobre from '../views/Sobre.vue'
import Contato from '../views/Contato.vue'
import Blog from '../views/Blog.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'Home',
    component: Home
  },
  {
    path: '/Promotor',
    name: 'Promotor',
    component: Promotor
  },
  {
    path: '/Caixa',
    name: 'Caixa',
    component: Caixa
  },
  {
    path: '/Sobre',
    name: 'Sobre',
    component: Sobre
  },
  {
    path: '/Contato',
    name: 'Contato',
    component: Contato
  },
  {
    path: '/Blog',
    name: 'Blog',
    component: Blog
  },
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
